.App {
  padding: 0;
  width: 100%;
  height: 100vh;
  margin-bottom: 50vh;
}

.App-logo {
  height: 2.5vh;
  width: auto;
  vertical-align: middle;
  align-items: center;
  padding-bottom: 0.5vh;
}

.App-link {
  font-family: 'Hanken Grotesk', sans-serif;
  font-weight: 300;
  font-size: 0.8rem;
  letter-spacing: 0.1rem;
  color: #464648;
  text-decoration: none;
  padding-top: 0.5vh;
}

.hero-bg {
  background-image: url("../public/img/background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  align-self: top;
  z-index: 1;
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 10vw 0 10vw;
}

.scroll-overlay {
  background-color: white;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  padding: 10vh 0;
  width: 100%;
}

.hero-header {
  padding-left: 0.115vw;
  padding-top: 40vh;
}

.hero {
  background-color: transparent;
  background-size: cover;
  padding: 0 15vw 0 30vw;
  height: 100vh;
}

h1, header {
  font-family: 'Hanken Grotesk';
  font-size: 2.8rem;
  font-weight: 300;
  letter-spacing: 0.1rem;
  line-height: normal;
}
