h6 {
  font-family: 'Roboto', sans-serif;
  font-size: 0.5rem;
  line-height: 0.8rem;
  letter-spacing: 1rem;
  color: #000000;
}

@media screen and (min-width: 1024px) {
  h5 {
    font-family: 'Hanken Grotesk', sans-serif;
    font-size: 1.4rem;
    line-height: 1.6rem;
    letter-spacing: 0.1rem;
    font-weight: 300;
    color: #2A2A2C;
  }

  h2 {
      font-family: 'Roboto', sans-serif;
      font-size: 2.8rem;
      line-height: 3.5rem;
      letter-spacing: 0.1rem;
      font-weight: 500;
      color: #464648;
  }
}

@media screen and (max-width: 1024px) {
  h5 {
    font-family: 'Hanken Grotesk', sans-serif;
    font-size: 1.4rem;
    line-height: 1.6rem;
    letter-spacing: 0.1rem;
    font-weight: 300;
    color: #2A2A2C;
  }

  h2 {
      font-family: 'Roboto', sans-serif;
      font-size: 2.8rem;
      line-height: 3.5rem;
      letter-spacing: 0.1rem;
      font-weight: 500;
      color: #464648;
  }
}

@media screen and (min-width: 768px) {
  .scroll-overlay {
    padding: 0;
    width: 100%;
  }
}

@media screen and (min-width: 568px) {
  .scroll-overlay {
    width: 100%;
    padding: 0;
  }
  .hero {
    padding: 0 20vw 0 20vw;
  }
}

@media screen and (min-width: 320px) {
  .scroll-overlay {
    width: 100%;
    padding: 0;
  }
  .hero {
    padding: 0 15vw 0 15vw;
  }
}





.section {
  
  margin: 5vh 0;
    padding-right: 12vw;
    padding-left: 15vw;
    height: 100vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: right;
    background-color: rgba(255, 255, 255, 1);

}
.card-right {
  position: absolute;
  top: 199px;
  left: 612px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  width: 50vw;
  height: 60vh;
  padding: 56px;
  border-radius: 20px;
  box-sizing: border-box;
  background-color: rgba(244, 244, 244, 1);
}
.header {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-start;
  gap: 10px;
  width: fit-content;
  box-sizing: border-box;
}
.fishbowl-go {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: rgba(63, 63, 63, 1);
  font-size: 20px;
  line-height: 20px;
  font-family: Poppins, sans-serif;
  font-weight: 400;
  text-align: right;
  letter-spacing: 1px;
}

.paragraph {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-start;
  gap: 10px;
  width: fit-content;
  box-sizing: border-box;
}
.simplifying-inventory {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: rgba(63, 63, 63, 1);
  font-size: 24px;
  line-height: 24px;
  font-family: Poppins, sans-serif;
  font-weight: 600;
  text-align: right;
  letter-spacing: 1.2px;
}

.fishbowl-1 {
  position: absolute;
  top: 216px;
