/*list of projects by cards*/


/* -parent element*/

.portfolio-section {
    padding: 0 10vw;
}

#shadow {
    -webkit-filter: drop-shadow(2rem 4rem 2rem #8d99ae79 );
    filter: drop-shadow(2rem 3rem 2rem #8d99aea4);
    z-index: 6;
    overflow: visible;
}

.card.slide-in {
    opacity: 1;
    transform: translateY(0);
}

.card:hover .card-img {
    transform: translateY(-55%);
    z-index: 2;
}

.card-wrap:hover .card {  
    transform: scale(0.9);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.card.slide-in .header {
    transform: translateX(-100%);
}

.card.slide-in .content {
    transform: translateX(100%);
}

.card-link {
    display: block;
    text-decoration: none;
    margin: 0;
    padding: 0;
}

@media (max-width: 768px) {
    .card.slide-in .header {
    transform: translateY(-100%);
    }
    .card.slide-in .content {
    transform: translateY(100%);
    }
}

@media screen and (min-width: 1024px) {

    .portfolio-section > .casestudy:nth-child(even) {
        margin: 5vh 0;
        height: 100vh;
    }
      
    .portfolio-section > .casestudy:nth-child(odd) {
        margin: 5vh 0;
        height: 100vh;
    }
      
    .portfolio-section > .casestudy:nth-child(even) .card-link .project {
        margin: 5vh 0;
        height: 100vh;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: left;
    }
      
    .portfolio-section > .casestudy:nth-child(odd) .card-link .project {
        margin: 5vh 0;
        height: 100vh;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: right;
    }

    .card-wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        position: relative;
        width: 40vw;
        height: 60vh;
        margin: 0 auto;
        border-radius: 20px;
        overflow: visible;
    }
      
      .portfolio-section > .casestudy:nth-child(even) .card-link .project .card-wrap {
        flex-direction: row-reverse;
        text-align: left;
        padding-right: 12vw;
      }
      
      .portfolio-section > .casestudy:nth-child(odd) .card-link .project .card-wrap {
        flex-direction: row;
        text-align: right;
        padding-left: 12vw;

      }
      
      .card {
        display: flex;
        flex-direction: row;
        align-items: center;
      }
      
      .portfolio-section > .casestudy:nth-child(even) .card-link .project .card-wrap .card {
        position: relative;
        width: 50vw;
        height: 60vh;
        margin: 0 auto;
        background-color: #f1f4f7;
        border-radius: 20px;
        overflow: visible;
        flex: 0 0 240px;
        transition: all 0.3s ease;
        transform: translateY(50px);
        transition: transform 0.5s ease;
        z-index: 1;
        padding: 2vh 0 0 2vw;
      }
      
      .portfolio-section > .casestudy:nth-child(odd) .card-link .project .card-wrap .card {
        position: relative;
        width: 50vw;
        height: 60vh;
        margin: 0 auto;
        background-color: #f1f4f7;
        border-radius: 20px;
        overflow: visible;
        flex: 0 0 240px;
        transition: all 0.3s ease;
        transform: translateY(50px);
        transition: transform 0.5s ease;
        z-index: 1;
        padding: 2vh 2vw 0 0;
      }
      
      .card-img {
        position: absolute;
        top: 60%;
        transform: translateY(-50%);
        z-index: -1;
      }
      
      .casestudy:nth-child(odd) .card-link .project .card-img {
        left: -40%;
        max-width: 55vw;
      }
      
      .casestudy:nth-child(even) .card-link .project .card-img {
        right: -42%;
        max-width: 60vw;
      }
      
      .portfolio-section > .casestudy:nth-child(even) .card-link .project .card-wrap .card .card-info {
        padding: 4vh 20vw 4vh 4vw;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        height: 90%;
      }
      
      .portfolio-section > .casestudy:nth-child(odd) .card-link .project .card-wrap .card .card-info {
        padding: 4vh 4vw 4vh 20vw;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        height: 90%;
      }

}

