.proj1-bg {
    background-image: url("../../public/img/proj1-background.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    align-self: top;
    z-index: 1;
  }

  .hero-proj1 {
    background-color: transparent;
    background-size: cover;
    padding: 0 40vw 0 15vw;
    height: 100vh;
  }
  .proj1-header {
    padding-left: 0.115vw;
    padding-top: 40vh;
  }

  .proj-title {
    padding: 16vh 0 4vh 0;
  }

  .h5-proj1 {
    font-family: 'Hanken Grotesk', sans-serif;
    font-size: 1.4rem;
    line-height: 1.8rem;
    letter-spacing: 0.1rem;
    font-weight: 300;
    color: #ffffff;
  }

.h2-proj1 {
      font-family: 'Roboto', sans-serif;
      font-size: 2.8rem;
      line-height: 3.5rem;
      letter-spacing: 0.1rem;
      font-weight: 500;
      color: #ffffff;
}

.proj-row {
    display: flex;
    flex-direction: row;
    align-items: top;
    justify-content: space-between;
}

.team-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 4vw;
}

.label {
    display: flex;
    margin-right: 8vw;
    align-content: top;
}

.proj-description {
    display: flex;
    padding-right: 2vw;
    align-content: top;
    margin-left: 8vw;
}

.proj-team-container {
    display: flex;
    flex-direction: column;
    padding: 0 2vw;
    align-content: top;
}