/* styles/globalStyles.css */

/* Set default font family and size */
body, html {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Hanken Grotesk', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 16px;
  background: transparent;
  margin: 0;
  padding: 0;
  width: 100%;
}

/* Define color scheme */
:root {
  --primary-color: #4c566a; /* blackish */
  --secondary-color: #9EA3B0; /* grey */
  --tertiary-color: #8d99ae; /* light grey */
	--bg-color: #FAF3DD; /* tan */
	--text-color: #000000;  /* dark grey */
  --contrast-color: #2E3440;  /* dark grey */
}

/* Apply styles to links */
a {
  color: var(--primary-color);
  text-decoration: none;
  font-family: 'Hanken Grotesk', 'Roboto';
  font-weight: 300;
}

a:hover {
  text-decoration: none;
  color: var(--text-color);
}

